<template>
    <div class="register-page">
        <div class="form">
           <!-- <h2 class="form-title">用户注册</h2> -->
			<p class="form-title">用户注册</p>
         
                <div class="form-item">
                    <span class="form-item-title">账号</span>
                    <input type="text"
                           v-model="userInfo.account"
                           placeholder="请输入账号"
                           class="form-item-input">
                    </input>
                </div>
                <div class="form-item">
                    <span class="form-item-title">密码</span>
                    <input type="text"
                           v-model="userInfo.password"
                           placeholder="请输入账号"
                           class="form-item-input">
                    </input>
                </div>
                <div class="form-item">
                    <span class="form-item-title">确认密码</span>
                    <input type="text"
                           v-model="userInfo.re_password"
                           placeholder="请再次输入密码"
                           class="form-item-input">
                    </input>
                </div>
                <div class="form-btn">
<!--                    <button class="submit-btn" @click="submit">注册</button>-->
                    <el-button class="submit-btn" @click="submit">注册</el-button>
                    <p @click="toLogin">已有账号，去登录</p>
<!--				   <br>-->
<!--					<a @click="toLogin" href="www.baidu.com">已有账号，去登录</a>-->
                </div>
			</div>
        </div>
</template>

<script>
    import axios from 'axios'
    export default {
        name: "register",
        data(){
            return{
                userInfo:{
                    account:'',
                    password:'',
                    re_password: ''
                }
            }
        },
        methods:{
            submit(){
                if(this.userInfo.account==''||this.userInfo.password==''){
                    alert('注册失败，账号或密码不能为空')
                    this.userInfo.account=''
                    this.userInfo.password=''
                    this.userInfo.re_password=''
                    return
                }
                if(this.userInfo.account.length>10){
                    alert('注册失败，账号不能多于10位')
                    this.userInfo.account=''
                    this.userInfo.password=''
                    this.userInfo.re_password=''
                    return
                }
                if(this.userInfo.password.length<5){
                    alert('注册失败，密码不能少于5位')
                    this.userInfo.account=''
                    this.userInfo.password=''
                    this.userInfo.re_password=''
                    return
                }
                if(this.userInfo.password.length>10){
                    alert('注册失败，密码不能多于10位')
                    this.userInfo.account=''
                    this.userInfo.password=''
                    this.userInfo.re_password=''
                    return
                }
                if(this.userInfo.password!=this.userInfo.re_password){
                    alert('注册失败，两次输入密码不一致')
                    this.userInfo.account=''
                    this.userInfo.password=''
                    this.userInfo.re_password=''
                    return
                }
               this.$store.dispatch('registerListShow')
                alert('注册成功')
            },
            toLogin(){
                this.$store.commit('changeIsShow')
            }
        }
    }
</script>

<style scoped>
.register-page{
    padding-top: 40px;
/* 	background-color: antiquewhite;
 */}
.form{
/* 	background-color: aquamarine;
 */	width: 600px;
	box-shadow: 0 0 30px #ccc;
	margin: auto;
	padding: 0 30px 0 15px;
}
.form-title{
	font-size: 26px;
	font-weight: bold;
	text-align: center;
	line-height: 80px;
	height: 80px;
}
.form-item{
/* 	background-color: aqua;
 */	height: 40px;
	padding: 10px 0;
	display: flex;
	justify-content: center;
	
}
.form-item-title{
/* 	background-color: aquamarine;
 */	width: 120px;
	display: block;
	line-height: 40px;
	text-align: right;
	padding-right: 20px;
}
.form-item-input{
	flex: 1;
	font-size: 16px;
	padding: 0 5px;
	border: none;
	border-bottom: 1px dotted #ccc;
	
}
.form-btn{
/* 	background-color: antiquewhite; */
	padding:  30px;
	font-size: 22px;
	text-align: center;
	
}
.submit-btn{
	height: 39px;
	width: 90px;
	
}
.form-btn p{
	font-size: 10px;
	cursor: pointer;
	color: darkblue;
}

</style>