<template>
	<div class="login-page">
		<div class="form">
			<p class="title">管理后台</p>
			<div class="form-item">
				<el-input v-model="userInfo.account"
						  prefix-icon="el-icon-user"
						  placeholder="账号"
						  class="el-input">
				</el-input>
			</div>
			<div class="form-item">
				<el-input v-model="userInfo.password"
						  prefix-icon="el-icon-lock"
						  placeholder="密码"
						  type="password"
						  class="el-input">
				</el-input>
			</div>
			<div class="form-item">
				<el-button @click="registerShow" class="btn">注册</el-button>
				<el-button @click="login" class="btn">登录</el-button>
				
			</div>
		</div>
	</div>
</template>

<script>
	import axios from 'axios'
	export default{
		name:'Login',
		data(){
			return{
				userInfo:{
					account:'',
					password:''
				}
			}
		},
		methods:{
			login(){
				if(this.userInfo.account==''||this.userInfo.password==''){
					alert('账号或密码不能为空')
					this.userInfo.account=''
					this.userInfo.password=''
					return
				}
				if(this.userInfo.password.length<5){
					alert('密码不能少于5位')
					this.userInfo.account=''
					this.userInfo.password=''
					return
				}
				var information=[this.userInfo.account,this.userInfo.password]
				this.$store.dispatch('changeListShow',information)
			},
			registerShow(){
				this.$store.commit('changeIsShow')
			}
		}
	}
</script>

<style scoped>
