.form{
	background-color: rgb(0,29,41);
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	width: 620px;
	padding: 30px 50px 15px;
	margin: 280px auto;
	color: #fff;
	border-radius: 10px;
	box-shadow: 0 0 30px #ccc;
	
}
.title{
	/* background-color: aqua; */
	font-size: 20px;
	font-weight: bold;
	margin: 0;
	padding: 20px 0;
	text-align: center;
}
.form-item{
	/* background-color: aquamarine; */
	padding: 20px 60px;
	text-align: center;
}
.el-input{
	width: 84%;
	
}
.btn{
	width: 80px;
	margin-left: 15px;
	margin-right: 15px;
}
</style>