import Vue from "vue";
import vueRouter from "vue-router"
import App from './App'
import router from './router'
import store from "./store"
import {Button,Input,Table,TableColumn,Popover,Tag} from "element-ui";


Vue.config.productionTip=false
Vue.use(vueRouter)
Vue.use(Button)
Vue.use(Input)
Vue.use(Table)
Vue.use(TableColumn)
Vue.use(Popover)
Vue.use(Tag)
new Vue({
    el:"#app",
    render:h=>h(App),
    router,
    store,
    beforeCreate(){
        Vue.prototype.$bus=this
    }
})