import vueRouter from 'vue-router'
import add from "@/components/add";
import selection from "@/components/selection";
import xiugai from '@/components/Eidit'
export default new vueRouter({
    routes:[
		{
			name:'Add',
			path:'/add',
			component:add
		},
		{
			name:'Selection',
			path:'/selection',
			component:selection
		},
		{
			name:'xiugai',
			path: '/xiugai',
			component: xiugai
		}
    ]
})