<template>
    <div class="select-page">
        <div class="container">
            <p class="title">查询</p>
            <div class="selection">
                <el-input type="text" placeholder="输入要查询的学号" prefix-icon="el-icon-search" v-model="id"></el-input>
            </div>
            <div class="bottom">
                <el-button size="small" type="primary" @click="selected">查询</el-button>
            </div>
        </div>

    </div>
</template>

<script>
    export default {
        name: "selection",
        data(){
            return{
                id:''
            }
        },
        methods:{
            selected(){
                this.$store.commit('getId',this.id)
                this.id=''
            },

        },

    }
</script>

<style scoped>
    .select-page{
        background-color: #ffffff;
        padding-top: 5px;
        box-shadow: 0 30px 30px #ccc;
    }
    .container{
        width: 30%;
        margin: auto;
    }
    p{
        color: #888888;
    }
    .selection{
        margin: 20px 0;
    }
    .bottom{
        text-align: right;
        height: 50px;
    }
</style>